.card-project {
  display: flex;
  flex-direction: column;
  width: 254px;
  height: 307px;
  border-radius: 10px;
  background: #009FAC;
  background-image: url(../../images/Factory.svg);
  background-repeat: no-repeat;
  background-size: cover;
  margin-top: 0;
  margin-bottom: 0;
  margin-left: 0;
  margin-right: 30px;
  padding: 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  letter-spacing: 0.1px;
  color: #FFFFFF;
}

.card-project:last-child {
  margin-right: 0;
}

.card-project:hover{
  background: #E0F0FF;
  color: #001337;
  background-image: url(../../images/Factory.svg);
  background-repeat: no-repeat;
  background-size: cover;
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  .card-project {
    width: 140px;
    height: 130px;

  }
}

@media screen and (min-width: 768px) and ( max-width: 1023px)  {
  .card-project {
    width: 210px;
    height: 200px;
  }
}