.card__frame {
  margin-top: 20px;
  margin-bottom: 0;
  margin-left: 0;
  margin-right: 0;
  border-radius: 10px;
  padding: 0;
  height: 430px;
  max-width: 255px;
  width: 100%;
  z-index: 0;
  display: flex;
  flex-direction: column;
  box-shadow: 0px 4px 20px rgba(1, 54, 157, 0.15);
  color: #001337;
  background-image:  url(../../../images/covers/Project.svg);
  background-size: cover;
  background-repeat: no-repeat;
  transition: 0.5s ease-out;
}

.card__frame:hover {
  margin-top: 0;
  margin-bottom: 20px;
  color: #fff;
  height: 430px;
  transition: 0.5s ease-out;
}

.card__frame::before {
  content: '';
  z-index: 0;
  position: absolute;
  border-radius: 10px;
  height: 430px;
  max-width: 255px;
  width: 100%;
  background: linear-gradient(180deg, #FFFFFF 0%,
              rgba(255, 255, 255, 0) 100%);
}

.card__frame:hover::before {
  background: linear-gradient(180deg, #009FAC 0%,
              rgba(0, 159, 172, 0) 100%);
  background-size: cover;
  background-repeat: no-repeat;
}


@media screen and (min-width: 320px) and (max-width: 767px) {
  .card__frame {
    height: 244px;
    width: 140px;
    margin-left: 0;
    margin-right: 0;
    margin-bottom: 0;
    margin-top: 0;
  }

  .card__frame:hover {
    margin-top: 0;
    /* margin-bottom: 20px; */
    color: #fff;
    height: 244px;
    transition: 0.5s ease-out;
    margin-bottom: 0;
  }
  
  .card__frame::before {
    content: '';
    z-index: 0;
    position: absolute;
    border-radius: 10px;
    height: 244px;
    width: 140px;
    background: linear-gradient(180deg, #FFFFFF 0%,
                rgba(255, 255, 255, 0) 100%);
  }
  
  .card__frame:hover::before {
    background: linear-gradient(180deg, #009FAC 0%,
                rgba(0, 159, 172, 0) 100%);
    background-size: cover;
    background-repeat: no-repeat;
  }
}

@media screen and (min-width: 768px) and ( max-width: 1023px)  {
  .card__frame {
    max-width: 150px;
    width: 100%;
    height: 242px;
  }

  .card__frame:hover {
    margin-top: 0;
    margin-bottom: 20px;
    color: #fff;
    height: 242px;
    transition: 0.5s ease-out;
  }
  
  .card__frame::before {
    content: '';
    z-index: 0;
    position: absolute;
    border-radius: 10px;
    height: 242px;
    max-width: 150px;
    width: 100%;
    background: linear-gradient(180deg, #FFFFFF 0%,
                rgba(255, 255, 255, 0) 100%);
  }
  
  .card__frame:hover::before {
    background: linear-gradient(180deg, #009FAC 0%,
                rgba(0, 159, 172, 0) 100%);
    background-size: cover;
    background-repeat: no-repeat;
  }
}