.section-industrial__title {
  margin-top: 80px;
  margin-bottom: 0;
  margin-left: auto;
  margin-right: auto;
  max-width: 1110px;
  width: 100%;
  font-weight: 600;
  font-size: 36px;
  line-height: 68px;
  text-align: center;
  letter-spacing: 0.1px;
  text-transform: uppercase;  
  color: #000D26;
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  .section-industrial__title {
    font-size: 16px;
    line-height: 22px;
    margin-top: 40px;
  }
}

@media screen and (min-width: 768px) and ( max-width: 1023px)  {
  .section-industrial__title {
    font-size: 24px;
    line-height: 33px;
    margin-top: 60px;
  }
}