.section-articles__title {
  display: flex;
  flex-direction: row;
  justify-content: center;
  font-weight: 600;
  font-size: 36px;
  line-height: 68px;
  text-align: center;
  letter-spacing: 0.1px;
  text-transform: uppercase;
  color: #001337;
  max-width: 1110px;
  /* width: 100%; */
  padding: 0;
  margin-top: 80px;
  margin-bottom: 0;
  margin-left: 165px;
  margin-right: 165px;
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  .section-articles__title {
    margin-left: 0;
    margin-right: 0;
    margin-top: 40px;
    margin-bottom: 0;
    max-width: 767px;
    font-size: 16px;
    line-height: 22px;
  }
}

@media screen and (min-width: 768px) and ( max-width: 1023px)  {
  .section-articles__title {
    /* max-width: 767px; */
    font-size: 24px;
    line-height: 32px;
    margin-left: 40px;
    margin-right: 40px;
    margin-top: 60px;
    margin-bottom: 0;
  }
}